table,
tr,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
  text-align: center;
}

table {
  position: absolute;
  left: 6%;
  bottom: 5%;
}

th, td {
  padding: 0.2em 0.6em;
}

tr {
  height: 24px;
}

@media (max-width: 420px) {
  table {
    bottom: 15%;
  }
}
